"use client"

import { motion } from "framer-motion"
import { Github } from "lucide-react"

export default function CVProjects() {
  const projects = [
    {
      title: "EnsemTrust: Fake news detection",
      period: "10/2025 - 12/2025",
      description:
        "Building a complete data platform using the Lakehouse architecture, Ensemble Learning models to accurately classify news articles as genuine or fake and enabling analysis for reporting",
      technologies: [
        "Dagster",
        "Apache Spark",
        "MinIO",
        "LightGBM",
        "SVM",
        "Logistic Regression",
        "Metabase",
        "Streamlit",
      ],
      outcome:
        "Provided a data platform for news storage and reporting served through Metabase, and a Streamlit application that receives news input and responds whether it is fake or true, verifying its credibility.",
      role: "Data Engineer",
      teamSize: "3",
      github: "https://github.com/MinhTuan2405/EnsemTrust",
    },
    {
      title: "[AIDE] linkedIn_job_dataplatform",
      period: "8/2025 - 10/2025",
      description:
        "Building a data platform analyzing 100,000+ LinkedIn job postings (2023–2024), providing insights into hiring trends and workforce demand",
      technologies: ["Dagster", "DBT", "Apache Spark", "PostgreSQL", "MinIO", "Metabase"],
      outcome:
        "Analyzed recruitment and salary trends by company, industry, skill, and location to support job seekers, employers, and training institutions and served analytics through Metabase.",
      role: "Data Engineer",
      teamSize: "1",
      github: "https://github.com/MinhTuan2405/linkedIn_job_datapl4tform",
    },
    {
      title: "BikeStoreShop Backend API",
      period: "11/2025 - 12/2025",
      description:
        "Building a RESTful API server for bicycle store management system with 54 endpoints, handling product management, order processing, customer management, and business analytics",
      technologies: [
        "FastAPI",
        "PostgreSQL",
        "SQLAlchemy",
        "Alembic",
        "JWT",
        "Pytest",
        "Pydantic",
        "Uvicorn",
      ],
      outcome:
        "Delivered a complete backend system with authentication/authorization, product catalog management, order processing, customer relationship management, and 13 comprehensive business analytics endpoints for sales reporting and insights.",
      role: "Backend Developer",
      teamSize: "2",
      github: "https://github.com/MinhTuan2405/LightWeightBikeStore"
    },
  ]

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.15,
        delayChildren: 0.1,
      },
    },
  }

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 },
  }

  return (
    <motion.section variants={containerVariants} initial="hidden" animate="visible" className="space-y-8 sm:space-y-10">
      <h2 className="text-2xl sm:text-3xl font-bold text-foreground mb-4 sm:mb-6">Projects</h2>

      <div className="space-y-4 sm:space-y-6">
        {projects.map((project, index) => (
          <motion.div
            key={index}
            variants={itemVariants}
            className="glass-card rounded-xl sm:rounded-2xl p-5 sm:p-6 hover-lift relative overflow-hidden group"
            whileHover={{ y: -8 }}
          >
            {/* Shimmer effect */}
            <motion.div
              className="absolute inset-0 shimmer opacity-0 group-hover:opacity-100"
              style={{
                background: "linear-gradient(90deg, transparent, oklch(0.8 0.15 200 / 0.08), transparent)"
              }}
            />
            
            {/* Glow effect */}
            <motion.div
              className="absolute inset-0 rounded-xl sm:rounded-2xl opacity-0 group-hover:opacity-100 transition-opacity duration-500"
              style={{
                background: "radial-gradient(circle at 20% 50%, oklch(0.65 0.18 200 / 0.12) 0%, transparent 70%)"
              }}
            />
            
            <div className="relative z-10">
              <div className="flex flex-col sm:flex-row sm:items-start sm:justify-between gap-3 mb-4">
                <div className="flex-1">
                  <h3 className="text-lg sm:text-xl font-semibold text-foreground">{project.title}</h3>
                  <p className="text-xs sm:text-sm text-muted-foreground mt-1">
                    {project.role} • Team size: {project.teamSize}
                  </p>
                </div>
                <motion.span
                  className="text-xs sm:text-sm text-muted-foreground glass-light px-3 py-1.5 rounded-full self-start whitespace-nowrap"
                  whileHover={{ scale: 1.05 }}
                >
                  {project.period}
                </motion.span>
              </div>

              <p className="text-sm sm:text-base text-muted-foreground mb-4">{project.description}</p>

              <div className="mb-4">
                <p className="text-xs sm:text-sm font-semibold text-foreground mb-2">Technologies:</p>
                <div className="flex flex-wrap gap-1.5 sm:gap-2">
                  {project.technologies.map((tech) => (
                    <motion.span
                      key={tech}
                      className="glass-button text-primary px-2.5 py-1 rounded-full text-xs font-medium relative overflow-hidden"
                      whileHover={{ scale: 1.05 }}
                      whileTap={{ scale: 0.98 }}
                    >
                      {/* Badge glow on hover */}
                      <motion.div
                        className="absolute inset-0 rounded-full opacity-0 hover:opacity-100 transition-opacity duration-300"
                        style={{
                          background: "radial-gradient(circle at 50% 50%, oklch(0.65 0.18 200 / 0.15) 0%, transparent 70%)"
                        }}
                      />
                      <span className="relative z-10">{tech}</span>
                    </motion.span>
                  ))}
                </div>
              </div>

              <div className="mb-4 pb-4 border-b border-border/20">
                <p className="text-xs sm:text-sm font-semibold text-foreground mb-2">Outcome:</p>
                <p className="text-xs sm:text-sm text-muted-foreground leading-relaxed">{project.outcome}</p>
              </div>

              <motion.a
                href={project.github}
                target="_blank"
                rel="noopener noreferrer"
                className="inline-flex items-center gap-2 text-accent hover:text-accent/80 transition-smooth text-sm relative group/link"
                whileHover={{ x: 4 }}
                whileTap={{ scale: 0.98 }}
              >
                <motion.div
                  className="absolute -inset-2 rounded-lg opacity-0 group-hover/link:opacity-100 transition-opacity duration-300"
                  style={{
                    background: "radial-gradient(circle at 0% 50%, oklch(0.65 0.18 200 / 0.08) 0%, transparent 100%)"
                  }}
                />
                <Github className="w-4 h-4 relative z-10" />
                <span className="relative z-10">View on GitHub</span>
              </motion.a>
            </div>
          </motion.div>
        ))}
      </div>
    </motion.section>
  )
}
